@import url('https://fonts.googleapis.com/css?family=Audiowide');

.body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.page-display {
    width: 100%;
    height: 100%;
}

.body-container {
    display: inline-flex;
    background-color: #0d1019;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 88vh;
}

.content-box-option {
    z-index: +1;
    position: relative;
    width: 25%;
    height: 70%;
}

.sidebar-nav {
    justify-content: space-evenly;
    align-items: center;
    width: 18vw;
    height: 90vh;
    border-right: thick solid #0984e3;
    padding-right: 1vw;
}

.sidebar-nav button{
    width: 100%;
    margin: auto;
    height: 22%;
    font-size: 2vh;
    font-family: 'Audiowide';
    background-color: #353b48;;
    color: white;
    border: 1px solid #0984e3;
    border-radius: 5vh;
    margin-bottom: 1vh;
    margin-top: 1vh;
}

.alert-div {
    display: block;
    margin: auto;
    width: 100%;
    color: white;
    position: absolute;
    z-index: 1;
}


@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}
