@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    
}

.tool-page {
    width: 100%;
    height: 100%;
    font-family: 'Audiowide';
}

.tool-input-div {
    position: absolute;
    left: 0;
    width: 25%;
    height: 90vh;
    background-color: #0a0d14;
    border-right: .25vh solid #353b48;
}

.tool-header {
    padding: 2vh;
    width: auto;
    height: auto;
    text-align: center;

}

.tool-header h3 {
    font-size: bold;
    color: white;
    font-size: 3vh;
    padding-bottom: 1vh;
    border-bottom: .25vh solid #868991;
}

.tool-body {
    display: inline-block;
    width: 100%;
    height: 20vh;
}

.tool-games {
    width: 75%;
    height: 50vh;
    margin: auto;
}

.tool-container {
    width: auto;
    height: auto;
    margin: auto;
    margin-top: 2vh;
    text-align: center; 
}

.tool-output-div {
    position: absolute;
    right: 0;
    top: 0;
    width: 75%;
    height: 100vh;
}

.nav-container {
    position: relative;
    width: 100%;
    height: 10vh;
}

.load-screen {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.load-div {
    width: 50vh;
    height: 50vh;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    border: .75vh dotted #fff;
    border-color: transparent transparent transparent transparent;
    
}

.load-div h3 {
    font-size: 3.5vh;
    color: white;
    opacity: 0;
}

.display-div {
    padding: 5vh;
    padding-top: 15vh;
    display: none;
    width: auto;
    height: 100%;
}

.tool-logo {
    width: auto;
    height: 75%;
}

.configure-div {
    position: relative;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-family: 'Audiowide';
}

.configure {
    border: 1px solid #fff;
    padding: 5px;
    width: 66%;
    height: auto;
    max-height: 60%;
    overflow-y: scroll;
    margin: auto;
}

.configure h3, h4 {
    color: white;
    font-weight: 100;
}


@keyframes loading_spin {
    25% {
        border-color: #fff transparent transparent transparent;
    }
    50% {
        border-color: transparent white transparent transparent;
    }
    75% {
        border-color: transparent transparent #fff transparent;
    }
    100% {
        border-color: transparent transparent transparent #fff;
    }
}

@keyframes loading_fade_in {
    25% {
        opacity: .75;
    }
    50% {
        opacity: .5;
    }
    75% {
        opacity: .25;
    }
    100% {
        opacity: 0;
    }
}
  
@media (max-width: 800px) {
    .tool-input-div {
        width: 100%;
        position: relative;
        border-bottom: 1px solid grey;
        height: auto;
    }

    .load-div {
        width: 100%;
    }

    .tool-output-div {
        width: 100%;
        position: relative;
    }

    .tool-body {
        height: auto;
    }

    .tool-games {
        height: 100%;
    }
}
