@import url('https://fonts.googleapis.com/css?family=Audiowide');

.game-selection-div {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: "Audiowide";
    display: inline-block;
    background-color: #0d1019;
    border: .1vh solid #868991;
    border-radius: 2vh;
}

.game-selection-header {
    display: inline-flex;
    justify-content: center;
    padding: 1vh;
    width: 100%;
    height: auto;
}

.game-selection-header h2 {
    color: white;
    border-bottom: .1vh solid #0984e3;
    margin: 1vh;
    font-size: 3vh;
}   

.game-selection-body p {
    text-align: center;
    font-weight: 100;
    font-style: italic;
    color: #868991;
    margin: 0;
    font-size: 1.5vh;
}   


.game-selection-body {
    margin: auto;
    width: 95%;
    height: auto;
}

.game-select-labels {
    color: white;
    padding-left: 1vh;
    height: auto;
}

.select-search {
    width: 95%;
    border-bottom: .25vh solid #0984e3;
    border-radius: 1vh;
    padding: 1vh;
    margin-bottom: 5%;
}